<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import apolloProvider from '@/utils/apollo';
import gql from "graphql-tag";
import config from "@/utils/config";
import Clipboard from 'clipboard'
import { Toast } from 'vant';
import pairAbi from "@/utils/pair";
import tokenAbi from "@/utils/abi";
import UvStakeAbi from "@/utils/UvStakeAbi";
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      loading: false,
      finished: false,
      limit: 6,
      config: config,
      page: 1,
      list: [],
      pairContract: null,
      UvContract: null,
      UvStakeContract: null,
      total: '0.00',
      blackHole: '0.00',
      uvPrice: '0.00',
      lpInfo: {
        token0: '',
        token1: '',
        reserve0: '',
        reserve1: ''
      },
      initContract: () => {
        data.pairContract = new data.web3.eth.Contract(pairAbi, config.uvLpUsdt)
         data.UvContract = new data.web3.eth.Contract(tokenAbi, config.uvAddress)
         data.UvStakeContract = new data.web3.eth.Contract(UvStakeAbi, config.uvStakeAddress)
        data.initData()
      },
      initData: () => {
        data.getLpInfo()
        data.getUvInfo()
        data.getUvPrice()
      },
      getUvInfo: async () => {
        let total = await data.UvContract.methods.totalSupply().call()
        let blackHole = await data.UvContract.methods.balanceOf(config.burnAddress).call()
        data.total = Number(total/10**18).toFixed(3)
        data.blackHole = Number(blackHole/10**18).toFixed(3)
      },
      getUvPrice: async () => {
        let price = await data.UvStakeContract.methods.getNeedAmount(config.uvAddress, config.usdtAddress, 1 + '0'.repeat(18)).call()
        data.uvPrice = Number(price/10**18).toFixed(3)
      },
      getLpInfo: async () => {
        let reserve = await data.pairContract.methods.getReserves().call()
        let token0 = await data.pairContract.methods.token0().call()
        let token1 = await data.pairContract.methods.token1().call()
        data.lpInfo.reserve0 = Number(reserve._reserve0/10**18).toFixed(3)
        data.lpInfo.reserve1 = Number(reserve._reserve1/10**18).toFixed(3)
        data.lpInfo.token0 = token0
        data.lpInfo.token1 = token1
        await data.getList()
      },
      fresh: async () => {
        data.page = 1
        data.list = []
        await data.getList() 
      },
      onLoad: async () => {
        data.page = data.page + 1
        await data.getList()
      },
      getList: () => {
        data.loading = true
        apolloProvider.clients.uvPairClient.query({
          query: gql`query swapLists($first: Int!, $skip: Int! ) {
            swapLists(first: $first, orderBy: time, skip: $skip, orderDirection: desc) {
              id
              amount0In
              amount1In
              amount0Out
              amount1Out
              time
            }
          }`,
          variables: {
            first: data.limit,
            skip: (data.page - 1) * data.limit
          },
          fetchPolicy: 'no-cache'
        }).then((res) => {
          
          let list = res.data.swapLists
          if (list.length < data.limit) {
            data.finished = true;
          }
          data.list = [...data.list, ...list]

          data.loading = false
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
     onMounted(() => {
      var clipboard = new Clipboard('.clip-button')
      clipboard.on('success', function (e) {
        Toast(t('share.複製成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('share.複製失敗，請手動長按複製'))
      })
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
